import { useFormik } from "formik";
import * as Yup from "yup";
import axios from "axios";

function OrderForm() {
  // Phone number validation regex (Saudi Arabia format: +966XXXXXXXXX)
  const phoneRegExp = /^(\+966)?[0-9]{9,12}$/;

  // Formik setup
  const formik = useFormik({
    initialValues: {
      phone: "", // Changed from Phone to phone
      city: "",
      destination_country: "",
    },
    validationSchema: Yup.object({
      // phone: Yup.string()
      //   .matches(phoneRegExp, "        +966XXXXXXXXX")
      //   .required("  "),
      city: Yup.string().required(" "),
      destination_country: Yup.string().required(" "),
    }),
   
    onSubmit: async (values, { setSubmitting, resetForm }) => {
      console.log("Submitting Values:", values);
    
      try {
        const payload = {
          phone: values.phone, // Changed from Phone to phone
          city: values.city,
          destination_country: values.destination_country,
        };
        const response = await axios.post(`${process.env.REACT_APP_API_ENDPOINT}/crm/create-customer-wordpress/`, payload, {
          headers: {
            "Content-Type": "application/json",
          },
        });
    
        if (response.status === 200 || response.status === 201) {
          alert("   ! ");
          resetForm(); // Clear form after success
        } else {
          throw new Error("    .");
        }
      } catch (error) {
        console.error("API Error:", error.response);
        alert(`: ${error.response?.data?.phone?.[0] || "  .   ."}`);
      } finally {
        setSubmitting(false);
      }
    },
  });

  return (
    <div className="container py-5">
      <h1 className="text-center mb-5"> </h1>
      <div className="row justify-content-center">
        <div className="col-md-8">
          <div className="card">
            <div className="card-body">
              <h5 className="card-title mb-4">  </h5>
              <form onSubmit={formik.handleSubmit}>
                {/* Phone Input */}
                <div className="mb-3">
                  <label htmlFor="phone" className="form-label"> *</label>
                  <div className="input-group">
                    <input
                      type="text"
                      className="form-control"
                      id="phone"
                      placeholder=" 535 52353 966+"
                      value={formik.values.phone}
                      onChange={formik.handleChange}
                      onBlur={formik.handleBlur}
                      aria-required="true"
                      dir="rtl"
                      style={{ textAlign: "right" }}
                    />
                    {formik.touched.phone && formik.errors.phone && (
                      <div className="invalid-feedback">{formik.errors.phone}</div>
                    )}
                  </div>
                </div>

                {/* City Input */}
                <div className="mb-3">
                  <label htmlFor="city" className="form-label"> *</label>
                  <select
                    className={`form-select ${formik.touched.city && formik.errors.city ? "is-invalid" : ""}`}
                    id="city"
                    name="city"
                    value={formik.values.city}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    aria-required="true"
                  >
                    <option value=""> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                  </select>
                  {formik.touched.city && formik.errors.city && (
                    <div className="invalid-feedback">{formik.errors.city}</div>
                  )}
                </div>

                {/* Country Input */}
                <div className="mb-3">
                  <label htmlFor="destination_country" className="form-label"> *</label>
                  <select
                    className={`form-select ${formik.touched.destination_country && formik.errors.destination_country ? "is-invalid" : ""}`}
                    id="destination_country"
                    name="destination_country"
                    value={formik.values.destination_country}
                    onChange={formik.handleChange}
                    onBlur={formik.handleBlur}
                    aria-required="true"
                  >
                    <option value=""> </option>
                    <option value=" "></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value="Europe"> </option>
                    <option value=""></option>
                    <option value=""></option>
                    <option value=" "></option>
                    <option value=""></option>
                  </select>
                  {formik.touched.destination_country && formik.errors.destination_country && (
                    <div className="invalid-feedback">{formik.errors.destination_country}</div>
                  )}
                </div>

                {/* Submit Button */}
                <button type="submit" className="btn btn-danger w-100"> </button>
              </form>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OrderForm;